const LoaderWrapper = styled.div`
  svg#preloader-logo {
    height: 15vw;
    width: 15vw;
    min-width: 150px;
    min-height: 150px;
  }

  #outer-ring {
    transform-origin: center;
    animation: rotate 15s infinite linear;
  }

  .preloader.hidden svg {
    animation: scale-out 1s forwards;
  }

  #tree {
    transform-origin: center;
    animation: scale 560ms ease;
  }

  @keyframes rotate {
    0% {
      transform: rotate(0deg);
    }

    100% {
      transform: rotate(360deg);
    }
  }

  @keyframes scale {
    0% {
      transform: scale(0.7);
    }

    80% {
      transform: scale(1.01);
    }

    100% {
      transform: scale(1);
    }
  }

  @keyframes fadeOut {
    99% {
      opacity: 0;
    }
    100% {
      opacity: 0;
      display: none;
      z-index: -9999;
    }
  }

  @keyframes scale-out {
    100% {
      transform: scale(0);
    }
  }
`;
